$text-dark:#000000;
$text-secondary:gray;

.card {
    border:{
        radius:5px;
        width:1px;
        style:solid;
        color: lightgray;

    }
    .card__body{
        padding: 5px;
        display: flex;
        flex-direction: row-reverse;
        justify-content: space-between;
        figure{
            padding: 0;
            margin: 0 ;
            img{
                width: 163px;
                height: 163px ;
                border-radius: 5px;
            }
           
        }
    }
        .card__details{
            display: flex;
            flex-direction: column;
            .card__title{
                flex-grow: 1;
                font-size: 18px;
                font-weight: 600;
                color: $text-dark;
            }
            
            .card__price{
                color: gray;
            }
            .card__author{
                color: gray;
            }
            }
        
        }

    
